<template>
  <div class="BPMN" v-loading="isLoading">
    <el-container>
      <!--  -->
      <el-main class="container-box">
        <div class="containers" ref="content" :style="{'height':config.height}">
          <div class="canvas" ref="canvas" ></div>
          <div id="js-properties-panel" ref="panel" class="panel" v-show="isShowPanel"></div>
        </div>
      </el-main>
      <el-footer class="operate-box" height="20px" v-show="config.showDownloadLink">
        <div id="operate_bpmn" class="operate">
          <a ref="saveDiagram" href="javascript:">Xml</a>
          <a ref="saveSvg" href="javascript:">SVG</a>
        </div>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
  
  import Vue from 'vue'
  // 
  import BpmnViewer from 'bpmn-js/lib/NavigatedViewer'// 
  /*import BpmnViewer from 'bpmn-js/lib/Viewer'// */
  import BpmnModeler from 'bpmn-js/lib/Modeler'// 
  /*import BpmnModeler from './bpmn/customBpmn/custom/index.js'// */

  import BpmnModeling from 'bpmn-js/lib/features/modeling/Modeling'// Modeling

  import propertiesPanelModule from 'bpmn-js-properties-panel'// js

  // import propertiesProviderModule from 'bpmn-js-properties-panel/lib/provider/camunda/index.js'// (camunda)bpmn-js-properties-panel,propertiesPanelModule
  import propertiesProviderModule from './bpmn/resourse/activiti/index.js'// (activiti)bpmn-js-properties-panel,propertiesPanelModule

  // import activitiModdleDescriptor from './bpmn/resourse/camunda.json'// camunda
  import activitiModdleDescriptor from './bpmn/resourse/activiti.json'// activti

  // bpmn
  import { getBusinessObject } from 'bpmn-js/lib/util/ModelUtil';

  // .bpmn(diagrsamXML.bpmnraw-loader)

  // 
  import customElementsModule from './bpmn/customElements/index.js';// 
  import customTranslate from './bpmn/customTranslate/customTranslate.js';// 

  export default {
    name:'BPMN',
    data(){
      return {
        isLoading:false,
        bpmnModeler: null,// bpmn
        container: null,// 
        canvas: null,// 
        customTranslateModule:null,// 
        xmlStr:'',//XML
        xmlSvg:'',//SVG

        // 
        nodeCode: "",
        nodeName: "",
        // js
        isShowPanel:true,
        // 
        isShowPanelBtn:true,
      }
    },
    props: ['config'],
    methods:{  
      /**
       * 
       * @author chuanlin.Xiao
       * @date   2019-10-28T14:41:43+0800
       * @param  {[type]}                 xmlStr xml
       * @return {[type]}                           [description]
       */
      initModel(xmlStr) {
        this.buildModel(this.config.type,xmlStr);//
      },
      
      /*
       bpmnModel 
       *********************************************************/
      //
      buildModel(type,xmlStr){
        if(type==='Modeler'){//
          this.panel.innerHTML = "";//
          this.canvas.innerHTML = "";//
          this.isShowPanelBtn = true;
          this.isShowPanel = true;
          this.bpmnModeler = new BpmnModeler({
            container: this.canvas,
            keyboard: {
              bindTo: window
            },
            //
            propertiesPanel: {
              parent: '#js-properties-panel'
            },
            //
            additionalModules: [
              propertiesProviderModule,// propertiesPanelModule
              propertiesPanelModule,//

              this.customTranslateModule,// 
              customElementsModule,// 
            ],
            //
            moddleExtensions: {
              activiti: activitiModdleDescriptor
            },
          });
        }else{//
          this.panel.innerHTML = "";//
          this.canvas.innerHTML = "";//
          this.isShowPanelBtn = false;
          this.isShowPanel = false;
          this.bpmnModeler = new BpmnViewer({
            container: this.canvas,
            keyboard: {
              bindTo: document
            },
            bpmnRenderer: {//bpmn
              // defaultFillColor: '#333',
              // defaultStrokeColor: '#ff4500'
            },
            textRenderer: {//text
              defaultStyle: {
                fontFamily: '"Nothing You Could Do"',
                fontWeight: 'bold',
                fontSize: 12,
                // lineHeight: 16
              },
              externalStyle: {
                fontSize: 12,
                // lineHeight: 16
              }
            }
          });
        }

        // 
        let _this = this
        // adom
        const downloadLink = this.$refs.saveDiagram;
        const downloadSvgLink = this.$refs.saveSvg;
        // 
        this.bpmnModeler.on('commandStack.changed', function () {
          _this.saveDiagram(function (err, xml) {
            _this.setEncoded(downloadLink, 'activiti.bpmn', err ? null : xml)
          })

          _this.saveSVG(function (err, svg) {
            _this.setEncoded(downloadSvgLink, 'activiti.svg', err ? null : svg)
          })
        });

        //xmlxml 
        this.createNewDiagram(type,xmlStr); 
      },
      // 
      createNewDiagram(type,xmlStr) {
        const that = this;
        const bpmnXmlStr = xmlStr;
        // Xml (bpmnXmlStr)
        this.bpmnModeler.importXML(bpmnXmlStr, function (err) {
          if (err) {
            console.error(err);
            that.error(err)
          }else {
            // 
            that.success(type)
            // 
            // this.$parent.communicationEvent(data);
            // that.$emit('communicationEvent',data);
          }

          // 
          var canvas = that.bpmnModeler.get('canvas');
          canvas.zoom('fit-viewport');

          // 
          // const nodeCodes = ['StartEvent_1','Task_0qg0mca','Task_0307aue'];
          // const colorClass = 'nodeSuccess';
          // that.setNodeColor(nodeCodes, colorClass, canvas);
          // const nodeCodes2 = ['SequenceFlow_1u5gq9e','SequenceFlow_1n5pril'];
          // const colorClass2 = 'lineSuccess';
          // that.setNodeColor(nodeCodes2, colorClass2, canvas);
        })
      },
      // ()
      setNodeColor(nodeCodes, colorClass, canvas){
        for(let i = 0; i < nodeCodes.length; i++) {
          // canvas.addMarker(nodeCodes[i], colorClass);
        }
      },
      // 
      error(err) {
        Vue.prototype.$message.error({
            message: '',
            type: 'error'
        });
      },
      // 
      success(type) {
        const bpmnjs = this.bpmnModeler;
        const eventBus = bpmnjs.get('eventBus');
        const elementFactory = bpmnjs.get('elementFactory');
        if(type==='Modeler'){
          const commandStack = bpmnjs.get('commandStack');//Modeler
          const bpmnRules = bpmnjs.get('bpmnRules');//Modeler
          this.addBpmnListener(this, new BpmnModeling(eventBus,elementFactory,commandStack,bpmnRules));//Modeling
          this.dubbleQueryInfo();
          this.colorEvent();
        }else{
          // this.setLineColor('Task_0307aue','RED', this, new BpmnModeling(eventBus,elementFactory));
          this.addBpmnListener(this, new BpmnModeling(eventBus,elementFactory));//Modeling
        }    
      },
      // Modeling ,
      addBpmnListener(_self,modeling) {
        const bpmnjs = this.bpmnModeler;
        const eventBus = bpmnjs.get('eventBus');
        const events = [
          'element.click',
          'element.dblclick'
        ];
        events.forEach(function(event) {
          eventBus.on(event, function(e) {
            //bpmn:Lane bpmn:SequenceFlow bpmn:Task bpmn:SequenceFlow bpmn:ExclusiveGateway
            //bpmn:Process
            // console.log(event + '   ' + JSON.stringify(e.element));
            if(e.element.type=='bpmn:Process'||e.element.type=='bpmn:Lane')
              return;

            var elementRegistry = bpmnjs.get('elementRegistry');
            var shape = elementRegistry.get(e.element.id);
            // console.log(shape)
            // 
            // modeling.setColor(shape,{ stroke:'RED' });
            // id
            _self.nodeCode = shape.businessObject.id;
            _self.nodeName = shape.businessObject.name;
            console.log(shape.businessObject)  
            // console.log(_self.nodeCode)
            // console.log(_self.nodeName)
          })       
        })
      },
      //
      dubbleQueryInfo(){
				const that = this;
				const bpmnjs = this.bpmnModeler;
        const moddle = this.bpmnModeler.get('moddle'),
              modeling = this.bpmnModeler.get('modeling');
        this.bpmnModeler.on('element.contextmenu', 1500, (event) => {
          event.originalEvent.preventDefault();
          event.originalEvent.stopPropagation();
          if(event.element.type=='bpmn:Process'||event.element.type=='bpmn:Lane')
              return;
					var elementRegistry = bpmnjs.get('elementRegistry');
					var shape = elementRegistry.get(event.element.id);
					
					if(shape.businessObject.$type=='bpmn:UserTask'){// 
						that.$emit('communicationEvent',shape.businessObject,getBusinessObject);// 
					}
					
        });
      },
      //()
      colorEvent(){
        const viewer = this.bpmnModeler;
        var overlays = viewer.get('overlays'),
            canvas = viewer.get('canvas'),
            elementRegistry = viewer.get('elementRegistry'),
            modeling = viewer.get('modeling');

        // Option 1: Color via Overlay
        // var shape = elementRegistry.get('Task_1ymuvem');
        // console.log(shape)
        // var div = document.createElement("div");
        // div.className = 'highlight-overlay';
        // div.style.width = shape.width+'px';
        // div.style.height = shape.height+'px';
        // var $overlayHtml = div;
        // overlays.add('Task_1ymuvem', {
        //   position: {
        //     top: 0,
        //     left: 0
        //   },
        //   html: $overlayHtml
        // });

        // Option 2: Color via BPMN 2.0 Extension
        // var elementToColor = elementRegistry.get('StartEvent_1');
        // modeling.setColor([ elementToColor ], {
        //   stroke: 'green',
        //   fill: 'rgba(0, 80, 0, 0.4)'
        // });

        // Option 3: Color via Marker + CSS Styling
        // canvas.addMarker('Task_0ho18x0', 'highlight');
      },

      // SVG,done
      saveSVG (done) {
        // donebpmnsaveSVG
        this.bpmnModeler.saveSVG(done)
      },
      // Xml,done
      saveDiagram (done) {
        let this_ = this;
        // donebpmnsaveXML
        this.bpmnModeler.saveXML({ format: true }, function (err, xml) {
          done(err, xml)
        })
      },
      // dataxml
      setEncoded (link, name, data) {
        // xmlURI
        const encodedData = encodeURIComponent(data);
        // xmlxml
        name==="activiti.bpmn"?this.xmlStr = data : this.xmlSvg = data;
        // console.log(this.xmlStr)
        // ,aclassNameahrefdownload
        if (data) {
          link.className = 'active'
          link.href = 'data:application/bpmn20-xml;charset=UTF-8,' + encodedData
          link.download = name
        }
      },
    },
    mounted(){
      
      // refcontentdom
      this.container = this.$refs.content
      // refcanvasdom
      this.canvas = this.$refs.canvas;
      // refpaneldom
      this.panel = this.$refs.panel;
      // 
      this.customTranslateModule = {
        translate: [ 'value', customTranslate ]
      };
      //
      // this.initModel(this.config.xmlStr);

      // this.bpmnModeler.detach();//
      // this.bpmnModeler.attachTo('#el');//

      // configcommon.js
      this.common.bpmnConfig = this.config;
    }
  }
  
</script>

<style lang="less">
  @import './bpmn/bpmn.less';
</style>
